import React from 'react'
import Moment from 'react-moment'
import { Link } from 'gatsby'

import { faCubes, faHistory } from '@fortawesome/free-solid-svg-icons'
import { faClock } from '@fortawesome/free-regular-svg-icons'

import FAIcon from '@src-components/block/fa-icon'

import { ThumbnailImage } from '@src-components/common/image'

import ScreenReaderText from '@src-components/block/screen-reader-text'

import { AllCategoryContentQuery } from '@src-queries'

const ArticleListItem = props => {
	const { item } = props

	const allCategoryTypeData = AllCategoryContentQuery()

	return (
		<li>
			<article className="break-all">
				<Link to={item.node.fields.slug} className="block group">
					<figure>
						<div className="relative rounded overflow-hidden transition transform duration-150 ease-in-out">
							<ThumbnailImage
								filePath={item.node.frontmatter.thumbnail}
								alt={item.node.frontmatter.title}
								className="w-full h-auto"
							/>
							<div className="absolute inset-0 flex items-center justify-center text-center border border-gray-200 rounded-md opacity-15 transition ease-in-out duration-150"></div>
							<div className="absolute inset-0 bg-gray-50 opacity-0 group-hover:opacity-50 transition ease-in-out duration-150"></div>
						</div>
						<figcaption className="mt-2 group-hover:bg-gray-50 p-2 rounded-md">
							<p className="text-sm font-medium text-gray-900">
								{item.node.frontmatter.title}
							</p>
							<p className="text-sm text-gray-500">
								{item.node.frontmatter.description}
							</p>
							<div>
								{item.node.frontmatter.modifiedTime && (
									<div className="inline-block ml-4 first:ml-0">
										<p className="text-sm text-gray-900 flex items-center">
											<FAIcon icon={faHistory} size="sm" className="mr-1" />
											<Moment
												date={item.node.frontmatter.modifiedTime}
												format="YYYY年MM月DD日"
												className="inline-block"
											/>
										</p>
									</div>
								)}
								<div className="inline-block ml-4 first:ml-0">
									<p className="text-sm text-gray-900 flex items-center">
										<FAIcon icon={faClock} size="sm" className="mr-1" />
										<Moment
											date={item.node.frontmatter.publishTime}
											format="YYYY年MM月DD日"
											className="inline-block"
										/>
									</p>
								</div>
							</div>
							<div className="flex items-start text-sm text-gray-900">
								<dt>
									<FAIcon icon={faCubes} size="sm" />
									<ScreenReaderText>カテゴリー</ScreenReaderText>
								</dt>
								<dd>
									{item.node.frontmatter.categories.map(name => {
										const categoryItemData = allCategoryTypeData.find(
											data => data.node.fields.name === name
										)
										if (!categoryItemData) return null
										return (
											<span
												key={categoryItemData.node.id}
												className="inline-block  border border-gray-600 bg-white rounded-sm px-1 ml-1"
											>
												{categoryItemData.node.frontmatter.title}
											</span>
										)
									})}
								</dd>
							</div>
						</figcaption>
					</figure>
				</Link>
			</article>
		</li>
	)
}

export default ArticleListItem
