import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'gatsby'

import { faAngleLeft, faAngleRight } from '@fortawesome/free-solid-svg-icons'

import FAIcon from '@src-components/block/fa-icon'
import ScreenReaderText from '@src-components/block/screen-reader-text'

const Pager = props => {
	const {
		current,
		max,
		parentPath,
		prevPath,
		nextPath,
		hasPrev,
		hasNext,
	} = props

	return (
		<div className="text-center py-8">
			<nav className="mb-2">
				{hasPrev && (
					<>
						<Link
							className="border border-gray-600  rounded-sm text-gray-600 font-semibold py-1 px-2 mx-1 hover:border-gray-900 hover:text-gray-900 hover:shadow hover:bg-gray-50"
							to={parentPath}
						>
							最初のページ
						</Link>
						<Link
							className="border border-gray-600 rounded-sm text-gray-600 font-semibold py-1 px-2 mx-1 hover:border-gray-900 hover:text-gray-900 hover:shadow hover:bg-gray-50"
							to={prevPath}
						>
							<FAIcon icon={faAngleLeft} />
							<ScreenReaderText>前のページ</ScreenReaderText>
						</Link>
					</>
				)}
				{hasNext && (
					<>
						<Link
							className="border border-gray-600 rounded-sm text-gray-600 font-semibold py-1 px-2 mx-1 hover:border-gray-900 hover:text-gray-900 hover:shadow hover:bg-gray-50"
							to={nextPath}
						>
							<ScreenReaderText>次のページ</ScreenReaderText>
							<FAIcon icon={faAngleRight} />
						</Link>
						<Link
							className="border border-gray-600 rounded-sm text-gray-600 font-semibold py-1 px-2 mx-1 hover:border-gray-900 hover:text-gray-900 hover:shadow hover:bg-gray-50"
							to={`${parentPath}page/${max}/`}
						>
							最後のページ
						</Link>
					</>
				)}
			</nav>
			{max > 1 && (
				<span className="text-sm">
					{max} ページ中 {current} ページ目を表示しています
				</span>
			)}
		</div>
	)
}

Pager.propTypes = {
	current: PropTypes.number.isRequired,
	max: PropTypes.number.isRequired,
	parentPath: PropTypes.string.isRequired,
	prevPath: PropTypes.string.isRequired,
	nextPath: PropTypes.string.isRequired,
	hasPrev: PropTypes.bool.isRequired,
	hasNext: PropTypes.bool.isRequired,
}

export default Pager
