import React, { useState } from 'react'
import { graphql } from 'gatsby'
import PropTypes from 'prop-types'
import { faHome, faTag, faTags } from '@fortawesome/free-solid-svg-icons'

import AppContext from '@src-contexts/app-context'
import SEO from '@src-components/common/seo'

import Layout from '@src-components/layout'

import PageHeading from '@src-components/parts/page-heading'

import ArticleList, {
	ArticleListItem,
} from '@src-components/parts/article-list'

import Pager from '@src-components/parts/pager'

import { AllTagContentQuery } from '@src-queries'

const TagPaginationTemplate = props => {
	const { location, data, pageContext } = props

	const {
		tagName,
		currentPage,
		canonical,
		totalPage,
		prevPagePath,
		nextPagePath,
		hasPrevPage,
		hasNextPage,
	} = pageContext

	const allTagContentData = AllTagContentQuery()

	const tagData = allTagContentData.find(
		tagContentData => tagContentData.node.fields.name === tagName
	)

	const [isOpenMenu, setIsOpenMenu] = useState(false)
	const [isOpenSearch, setIsOpenSearch] = useState(false)

	const pageTitle = `タグ：${tagData.node.frontmatter.title}`
	const pageDescription = tagData.node.frontmatter.description

	const pageValues = {
		title: pageTitle,
		description: pageDescription,
		location: location.pathname,
		canonical: canonical,
		breadcrumbItems: [
			{ label: 'ホーム', url: '/', icon: faHome },
			{ label: 'タグ一覧', url: '/tags/', icon: faTags },
			{ label: pageTitle, url: location.pathname, icon: faTag },
		],
		isOpenMenu: isOpenMenu,
		setIsOpenMenu: setIsOpenMenu,
		isOpenSearch: isOpenSearch,
		setIsOpenSearch: setIsOpenSearch,
	}

	return (
		<AppContext.Provider value={pageValues}>
			<SEO />
			<Layout pattern="one-column">
				<PageHeading icon={faTag}>{pageTitle}</PageHeading>
				<p className="mt-8">{pageDescription}</p>
				<section className="mt-8">
					<ArticleList>
						{data.allMdx.edges.map(edge => {
							return <ArticleListItem item={edge} key={edge.node.id} />
						})}
					</ArticleList>
					<Pager
						current={currentPage}
						max={totalPage}
						parentPath={`/tag/${tagName}/`}
						prevPath={prevPagePath}
						nextPath={nextPagePath}
						hasPrev={hasPrevPage}
						hasNext={hasNextPage}
					/>
				</section>
			</Layout>
		</AppContext.Provider>
	)
}

TagPaginationTemplate.propTypes = {
	pageContext: PropTypes.object,
}

export default TagPaginationTemplate

export const pageQuery = graphql`
	query($tagName: String!, $limit: Int!, $offset: Int!) {
		allMdx(
			limit: $limit
			skip: $offset
			sort: { fields: [frontmatter___modifiedTime], order: DESC }
			filter: {
				frontmatter: {
					tags: { in: [$tagName] }
					type: { eq: "post" }
					draft: { ne: true }
				}
			}
		) {
			edges {
				node {
					id
					frontmatter {
						title
						description
						thumbnail
						categories
						tags
						publishTime
						modifiedTime
					}
					fields {
						slug
						name
					}
				}
			}
		}
	}
`
